import React, { useState, useEffect } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { useHttpClient } from '../hooks/http-hook';
import FeedbackDialog from './FeedbackDialog';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import PhoneIcon from '@material-ui/icons/Phone';
import EmailIcon from '@material-ui/icons/Email';
import StarIcon from '@material-ui/icons/Star';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import male from './male.png';
import female from './female.png';
import { Paper, Typography } from '@material-ui/core';
import BookAppointment from './BookAppointment';
import LoadingSpinner from '../utils/LoadingSpinner';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    arrowButton: {
        color: 'black',
        marginRight: theme.spacing(2),
    },
    margin: {
        margin: theme.spacing(1),
    },
    title: {
        flexGrow: 1,
    },
    grid: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    typo: {
        paddingLeft: '20px',
    }
}));

const ShowSingleDoctor = (props) => {
    const doctor = props.doctor;
    const classes = props.classes;

    return (
        <div>
            {doctor.gender === 'male' ? (
                <div style={{
                    backgroundColor: '#FF5B62',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center'
                }}>
                    <img width="200" src={male} alt="Male" />
                </div>
            ) : (
                    <div style={{
                        backgroundColor: '#FAD24D',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center'
                    }}>
                        <img width="200" src={female} alt="Female" />
                    </div>
                )}
            <Paper style={{ padding: '10px' }} elevation={3}>
                <Typography variant='h5' style={{ fontWeight: '550' }}>
                    Dr. {doctor.name}
                </Typography>
            </Paper>
            <div className={classes.root}>
                <Grid container spacing={0}>
                    <Grid className={classes.grid} item xs={3}>
                        <IconButton aria-label="whatsapp" className={classes.margin} href={`whatsapp://phone=${doctor.contact}`}>
                            <WhatsAppIcon fontSize="large" />
                        </IconButton>
                    </Grid>
                    <Grid className={classes.grid} item xs={3}>
                        <IconButton aria-label="phone" className={classes.margin} href={`tel:${doctor.contact}`}>
                            <PhoneIcon fontSize="large" />
                        </IconButton>
                    </Grid>
                    <Grid className={classes.grid} item xs={3}>
                        <IconButton aria-label="email" className={classes.margin} href={`mailto:${doctor.email}`}>
                            <EmailIcon fontSize="large" />
                        </IconButton>
                    </Grid>
                    <Grid className={classes.grid} item xs={3}>
                        <IconButton aria-label="rating" className={classes.margin} style={{ fontWeight: '600' }}>
                            3.8<StarIcon fontSize="large" />
                        </IconButton>
                    </Grid>
                </Grid>
                <Grid container spacing={0} style={{ marginTop: '30px', marginBottom: '80px' }}>
                    <Grid item xs={12}>
                        <Typography className={classes.typo} variant='h6'>
                            About Doctor
                        </Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Typography className={classes.typo} variant='h6'>
                            Education
                        </Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Typography className={classes.typo} variant='h6'>
                            Specialist Area
                        </Typography>
                    </Grid>
                </Grid>
                <Grid container spacing={0}>
                    <Grid className={classes.grid} item xs={5}>
                        <Button variant="contained" color="primary" onClick={props.handleClickFeedback}>
                            GIVE FEEDBACK
                        </Button>
                    </Grid>
                    <Grid className={classes.grid} item xs={7}>
                        <Button variant="contained" color="primary" onClick={props.handleClickBooking}>
                            BOOK APPOINTMENT
                        </Button>
                    </Grid>
                </Grid>
            </div>
        </div>);
};

const Doctor = () => {
    const classes = useStyles();
    const params = useParams('/doctor/:id');
    const { id } = params;
    let history = useHistory();
    // eslint-disable-next-line
    const [doctor, setDoctor] = useState(null);
    const { sendRequest } = useHttpClient();
    const [feedback, setFeedback] = useState(false);

    const [booking, setBooking] = useState(false);

    const handleClickFeedback = () => {
        setFeedback(true);
    };

    const handleClickBooking = () => {
        setBooking(true);
    };

    const handleCloseFeedback = () => {
        setFeedback(false);
    };

    const handleCloseBooking = () => {
        setBooking(false);
    };

    useEffect(() => {
        const fetchDoctors = async () => {
            try {
                const doctors = await sendRequest(
                    process.env.REACT_APP_BACKEND_URL + '/skinzy_app/get_doctor',
                );
                // const doctors = [
                //     {
                //         "name": "Amurto",
                //         "dob": 10102000,
                //         "gender": "male",
                //         "id": 1,
                //         "email": "test1@gmail.com",
                //         "contact": "123456789",
                //         "is_doctor": true,
                //         "email_key": "123456789",
                //         "doctor_upi_id": "abc@oksbi"
                //     },
                //     {
                //         "name": "Pratik",
                //         "dob": 10102000,
                //         "gender": "male",
                //         "id": 2,
                //         "email": "test1@gmail.com",
                //         "contact": "123456789",
                //         "is_doctor": true,
                //         "email_key": "123456789",
                //         "doctor_upi_id": "abc@oksbi"
                //     },
                //     {
                //         "name": "Shubham",
                //         "dob": 10102000,
                //         "gender": "male",
                //         "id": 3,
                //         "email": "test1@gmail.com",
                //         "contact": "123456789",
                //         "is_doctor": true,
                //         "email_key": "123456789",
                //         "doctor_upi_id": "abc@oksbi"
                //     },
                //     {
                //         "name": "Gavin",
                //         "dob": 10102000,
                //         "gender": "female",
                //         "id": 4,
                //         "email": "test1@gmail.com",
                //         "contact": "123456789",
                //         "is_doctor": true,
                //         "email_key": "123456789",
                //         "doctor_upi_id": "abc@oksbi"
                //     },
                // ]
                const uid = Number(id);
                const doctor = doctors.find(({ id }) => id === uid);
                setDoctor(doctor);
            } catch (err) { console.error(err); }
        };
        fetchDoctors();
    }, [sendRequest, id, setDoctor]);

    return (
        <React.Fragment>
            {!!doctor && feedback && <FeedbackDialog feedback={feedback} handleCloseFeedback={handleCloseFeedback} id={doctor.id} />}
            {!!doctor && booking && <BookAppointment doctor_id={doctor.id} booking={booking} handleCloseBooking={handleCloseBooking} id={doctor.id} />}
            <div className={classes.root}>
                <AppBar style={{ backgroundColor: '#FAFAFA' }} position="static">
                    <Toolbar>
                        <IconButton edge="start" className={classes.arrowButton} color="inherit" aria-label="arrow" onClick={() => { history.goBack() }}>
                            <ArrowBackIcon />
                        </IconButton>
                    </Toolbar>
                </AppBar>
                {!!doctor && <ShowSingleDoctor doctor={doctor} classes={classes} handleClickBooking={handleClickBooking} handleClickFeedback={handleClickFeedback} />}
                {!doctor && <LoadingSpinner size={100} thickness={4} />}
            </div>
        </React.Fragment>
    );
}

export default Doctor;