import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import { UploadProvider } from '../context/upload-context';
import UploadPages from './UploadPages';

const Upload = () => {
    const initialUpload = { initialImg: null, croppedImg: null, result: null }

    const reducer = (state, action) => {
        // let initialImg = state.initialImg;
        // let croppedImg = state.croppedImg;
        // let result = state.result;
        switch (action.type) {
            case 'changeInitialImg':
                return {
                    ...state,
                    initialImg: action.img
                };

            case 'cropImg':
                return {
                    ...state,
                    croppedImg: action.val
                };
            
            case 'setResult':
                return {
                    ...state,
                    result: action.val
                };
            
            case 'resetAll':
                return {
                    initialImg: null,
                    croppedImg: null,
                    result: null
                };
            
            default:
                return state;
        }
    };

    return (
        <UploadProvider initialUpload={initialUpload} reducer={reducer} >
            <CssBaseline />
            <Container>
                <UploadPages />
            </Container>
        </UploadProvider>
    )
}

export default Upload;
