import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useUpload } from '../context/upload-context';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
}));

const ResultsItem = props => {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <Paper style={{
                backgroundColor: '#067c91',
                color: 'white',
                borderRadius: '25px',
                padding: '10px',
                paddingLeft: '20px',
                paddingRight: '20px',
                marginBottom: '15px'
            }}>
                <Grid container spacing={1}>
                    <Grid item xs={6}>
                        {props.disease}
                    </Grid>
                    <Grid item xs={6} style={{ textAlign: 'right' }}>
                        {props.proba} %
                    </Grid>
                </Grid>
            </Paper>
        </div>
    )
}

const ResultsList = props => {
    return (
        <div>
            {props.items.map(result =>
                <ResultsItem
                    key={result.id}
                    id={result.id}
                    disease={result.disease}
                    proba={result.proba}
                />
            )}
        </div>
    )
}

const Results = () => {
    const [{ result }] = useUpload();

    let probas = []
    let keys = Object.keys(result);
    let mostDisease = keys[0]
    for (let i = 0; i < 3; i++) {
        probas.push({ id: i, disease: keys[i], proba: result[keys[i]].toFixed(2) })
    }

    return (
        <Container style={{
            marginTop: '100px',
        }}
            maxWidth='md'
        >
            {mostDisease === 'Normal Skin' && (
                <div style={{
                    borderRadius: '10px',
                    paddingTop: '10px',
                    paddingBottom: '10px',
                    marginBottom: '30px',
                    backgroundColor: '#3F51B5',
                    textAlign: 'center',
                    color: 'white'
                }}>
                    We think that your skin is Normal.
                </div>
            )}
            <Container>
                {probas && <ResultsList items={probas} />}
            </Container>
            {mostDisease !== 'Normal Skin' && (
                <div style={{ marginTop: '70px', textAlign: 'center', color: '#3F51B5', fontSize: '15px' }}>
                    <p style={{ margin: '0px', fontWeight: '500' }}>You have a high probability of {mostDisease}.</p>
                    <p style={{ margin: '0px', fontWeight: '500' }}>Skinzy does not guarantee 100% detection rate.</p>
                    <p style={{ margin: '0px', fontWeight: '500' }}>Please contact a Dermatologist as soon as possible.</p>
                </div>
            )}
        </Container>
    )
}

export default Results;