import React from 'react';
import { useUpload } from '../context/upload-context';
import Cropper from './Cropper';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';

const useStyles = makeStyles(theme => ({
  appBar: {
    position: 'relative',
    backgroundColor: '#404040',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const CropDialog = props => {
    const classes = useStyles();

    // eslint-disable-next-line
    const [{ initialImg, croppedImg, result }, dispatch] = useUpload();

    return (
        <Dialog style={{ background: 'black !important' }} fullScreen open={props.open} onClose={props.handleClose} TransitionComponent={Transition}>
          <div style={{ backgroundColor: 'black', minHeight: '100vh' }}>
          <AppBar className={classes.appBar}>
            <Toolbar>
                <IconButton edge="start" color="inherit" onClick={props.handleClose} aria-label="close">
                <CloseIcon />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                Crop
                </Typography>
                <Button autoFocus color="inherit" onClick={props.saveCrop}>
                save
                </Button>
            </Toolbar>
            </AppBar>
            <div style={{ 
                        backgroundColor: 'black', 
                        height: "80%", 
                        width: "95%", 
                        position: "fixed", 
                        top: "12%", 
                        left: "2.5%" 
                  }}>
                <Cropper croppedSrc={props.croppedSrc} setCroppedSrc={props.setCroppedSrc} className="react-crop-parent" />
            </div>
          </div>    
        </Dialog>
    );
}

export default CropDialog;