import React from 'react';
import {
  HashRouter as Router,
  Route,
  Redirect,
  Switch
} from 'react-router-dom';

import { ThemeProvider } from '@material-ui/styles';
import customTheme from './components/navigation/theme.json';
import { createMuiTheme } from '@material-ui/core/styles';

import Register from './components/register/Register.js';

import ResponsiveDrawer from './components/navigation/ResponsiveDrawer';
import Profile from './components/utils/Profile';
import Upload from './components/upload/Upload';
import Appointments from './components/appointments/Appointments';
import AppointmentStatus from './components/appointments/AppointmentStatus';
import Prescription from './components/utils/Prescription';
import History from './components/utils/History';
import Report from './components/utils/Report';
import Feedback from './components/utils/Feedback';
import Contact from './components/utils/Contact';
import About from './components/utils/About';
import Doctor from './components/appointments/Doctor.js';
import { ShowIfAuth, ShowIfNoAuth } from './components/auth/auth-check';
import Login from './components/auth/login-ui';

const theme = createMuiTheme(customTheme);

const App = () => {
  let routes;

  routes = (
    <Switch>
      <Route path="/" exact>
        <Redirect to="/upload" />
      </Route>
      <Route path="/register" exact>
        <Register />
      </Route>
      <Route path="/profile" exact>
        <Profile />
      </Route>
      <Route path="/upload" exact>
        <Upload />
      </Route>
      <Route path="/appointments" exact>
        <Appointments />
      </Route>
      <Route path="/doctor/:id" exact>
        <Doctor />
      </Route>
      <Route path="/status" exact>
        <AppointmentStatus />
      </Route>
      <Route path="/prescription" exact>
        <Prescription />
      </Route>
      <Route path="/report" exact>
        <Report />
      </Route>
      <Route path="/history" exact>
        <History />
      </Route>
      <Route path="/feedback" exact>
        <Feedback />
      </Route>
      <Route path="/contact" exact>
        <Contact />
      </Route>
      <Route path="/about" exact>
        <About />
      </Route>
      <Redirect to="/" />
    </Switch>
  )

  return (
    <ThemeProvider theme={theme}>
      <Router>
        <ShowIfAuth>
          <ResponsiveDrawer>
            {routes}
          </ResponsiveDrawer>
        </ShowIfAuth>
        <ShowIfNoAuth>
          <Login></Login>
        </ShowIfNoAuth>
      </Router>
    </ThemeProvider>
  );
}

export default App;
