import React from 'react';
import { Formik } from 'formik';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import EmailIcon from '@material-ui/icons/Email';
import Call from '@material-ui/icons/Call';

import CurrentUser from '../auth/Current';

const useStyles = makeStyles(theme => ({
    root: {
        height: '100vh',
    },
    image: {
        backgroundImage: 'url(https://source.unsplash.com/random)',
        backgroundRepeat: 'no-repeat',
        backgroundColor:
            theme.palette.type === 'dark' ? theme.palette.grey[900] : theme.palette.grey[50],
        backgroundSize: 'cover',
        backgroundPosition: 'center',
    },
    paper: {
        margin: theme.spacing(8, 4),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    title: {
        flexGrow: 1,
    },
}));

const Contact = () => {
    const classes = useStyles();

    return (
        <React.Fragment>
            <CssBaseline />
            <AppBar style={{ backgroundColor: '#02053b' }} position="static">
                <Toolbar>
                    <Typography variant="h6" className={classes.title}>
                        Contact Us
                    </Typography>
                </Toolbar>
            </AppBar>
            <Container maxWidth='md'>
                <div style={{ marginTop: '10%' }}>
                    <Formik
                        initialValues={{
                            text: ''
                        }}
                        validate={values => {
                            const errors = {};

                            if (!values.text) {
                                errors.text = 'Required';
                            } else if (values.text.length < 6) {
                                errors.text = 'text should have atleast 6 characters'
                            }

                            return errors;
                        }}
                        onSubmit={(values, { setSubmitting }) => {
                            const submitFormHandler = async values => {
                                const user = CurrentUser();
                                const name = user.displayName;
                                const email = user.email;
                                const token = await user.getIdToken();
                                console.log(values, name, email, token);
                            }
                            submitFormHandler(values);
                        }}
                    >{({
                        values,
                        errors,
                        touched,
                        handleChange,
                        handleBlur,
                        handleSubmit,
                        isSubmitting,
                        setFieldValue
                    }) => (
                            <form className={classes.form} onSubmit={handleSubmit}>
                                <Grid item xs={12}>
                                    <TextField
                                        fullWidth
                                        multiline={true}
                                        label="Text"
                                        name="text"
                                        type="text"
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        value={values.text}
                                    />
                                    <div style={{ margin: "10px", color: "red" }}>
                                        {errors.text && touched.text && errors.text}
                                    </div>
                                </Grid>
                                <Button
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    style={{ backgroundColor: '#02053b', color: 'white' }}
                                    className={classes.submit}
                                    disabled={isSubmitting}
                                >
                                    Submit
                            </Button>
                                <Grid container>
                                </Grid>
                            </form>
                        )}
                    </Formik>
                    <Typography style={{ fontWeight: '600', fontSize: '38px', color: 'darkblue', textAlign: 'center', marginBottom: '15px' }}>
                        Get To Us
                    </Typography>


                    <Typography style={{ textAlign: 'center', marginBottom: '15px' }}>
                        <Call /> Main: +91 8657424574
                    </Typography>
                    <Typography style={{ textAlign: 'center', marginBottom: '15px' }}>
                        <EmailIcon />  drkalbande@skinzy.in
                    </Typography>
                </div>
            </Container>
        </React.Fragment >
    )
}

export default Contact;
