import React from 'react';
import { useHistory } from 'react-router-dom';

import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import Avatar from '@material-ui/core/Avatar';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import BackupIcon from '@material-ui/icons/Backup';
import AssignmentIndIcon from '@material-ui/icons/AssignmentInd';
import AssignmentIcon from '@material-ui/icons/Assignment';
import DescriptionIcon from '@material-ui/icons/Description';
// import ReceiptIcon from '@material-ui/icons/Receipt';
// import HistoryIcon from '@material-ui/icons/History';
// import FeedbackIcon from '@material-ui/icons/Feedback';
import PhoneIcon from '@material-ui/icons/Phone';
import InfoIcon from '@material-ui/icons/Info';
import PowerSettingsNew from "@material-ui/icons/PowerSettingsNew";


import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';

import CurrentUser, { LogOut } from "../auth/Current"

import { ShowIfAuth } from '../auth/auth-check';

const drawerWidth = 300;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  button: {
    margin: theme.spacing(1),
  },
  drawer: {
    [theme.breakpoints.up('lg')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
    [theme.breakpoints.up('lg')]: {
      paddingTop: "64px",
    },
  },
}));

const SideDrawer = props => {
  let history = useHistory();
  const classes = useStyles();
  const theme = useTheme();

  const pushLink = link => {
    history.push(link);
    props.handleDrawerToggle();
  }
  function UserDetails(params) {
    const user = CurrentUser();
    if (user == null || user.photoURL == null || user.displayName == null)
      return <>
        <ListItemIcon>
          <AccountCircleIcon color="primary" fontSize="large" />
        </ListItemIcon>
        <Typography color="primary" style={{ fontSize: '25px', fontWeight: '500' }}>
          User
        </Typography></>;
    else
      return <>
        <ListItemIcon>
          <Avatar alt={user.displayName} src={user.photoURL} />
        </ListItemIcon>
        <Typography color="primary" style={{ fontSize: '25px', fontWeight: '500' }}>
          {user.displayName}
        </Typography></>;
  }
  const drawer = (
    <div>
      <ListItem classes={{ root: classes.root }} button>
        <UserDetails />
      </ListItem>
      <Divider />
      <List>
        {/* <ListItem button onClick={() => { pushLink('/profile') }}>
          <ListItemIcon><AccountCircleIcon /></ListItemIcon>
          <ListItemText primary="Profile" />
        </ListItem> */}
        <ListItem button onClick={() => { pushLink('/upload') }}>
          <ListItemIcon><BackupIcon /></ListItemIcon>
          <ListItemText primary="Examine" />
        </ListItem>
        <ListItem button onClick={() => { pushLink('/appointments') }}>
          <ListItemIcon><AssignmentIndIcon /></ListItemIcon>
          <ListItemText primary="Appointment" />
        </ListItem>
        <ListItem button onClick={() => { pushLink('/status') }}>
          <ListItemIcon><AssignmentIcon /></ListItemIcon>
          <ListItemText primary="Appointment Status" />
        </ListItem>
        <ListItem button onClick={() => { pushLink('/prescription') }}>
          <ListItemIcon><DescriptionIcon /></ListItemIcon>
          <ListItemText primary="Prescription" />
        </ListItem>
        {/* <ListItem button onClick={() => { pushLink('/report') }}>
          <ListItemIcon><ReceiptIcon /></ListItemIcon>
          <ListItemText primary="Report" />
        </ListItem> */}
        {/* <ListItem button onClick={() => { pushLink('/history') }}>
          <ListItemIcon><HistoryIcon /></ListItemIcon>
          <ListItemText primary="History" />
        </ListItem> */}
        {/* <ListItem button onClick={() => { pushLink('/feedback') }}>
          <ListItemIcon><FeedbackIcon /></ListItemIcon>
          <ListItemText primary="Feedback" />
        </ListItem> */}
        <ListItem button onClick={() => { pushLink('/contact') }}>
          <ListItemIcon><PhoneIcon /></ListItemIcon>
          <ListItemText primary="Contact Us" />
        </ListItem>
        <ListItem button onClick={() => { pushLink('/about') }}>
          <ListItemIcon><InfoIcon /></ListItemIcon>
          <ListItemText primary="About Us" />
        </ListItem>
        <ListItem button onClick={() => {
          LogOut().then(() => {
            window.location.reload();
          })
        }}>
          <ListItemIcon><PowerSettingsNew /></ListItemIcon>
          <ListItemText primary="Logout" />
        </ListItem>
      </List>
    </div>
  );

  return (
    <nav className={classes.drawer} aria-label="mailbox folders">
      <ShowIfAuth>
        <Hidden lgUp>
          <Drawer
            container={props.container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={props.mobileOpen}
            onClose={props.handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden mdDown>
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </ShowIfAuth>
    </nav>
  );
}

export default SideDrawer;