import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useUpload } from '../context/upload-context';
import LoadingSpinner from '../utils/LoadingSpinner';
import CropDialog from './CropDialog';
import Results from './Results';
import Alert from '@material-ui/lab/Alert';
import Button from '@material-ui/core/Button';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary';
import CropIcon from '@material-ui/icons/Crop';
import ForwardIcon from '@material-ui/icons/Forward';
import Typography from '@material-ui/core/Typography';
import { useHttpClient } from '../hooks/http-hook';
// import CurrentUser from '../auth/Current';

const useStyles = makeStyles(theme => ({
    root: {
        '& > *': {
            margin: theme.spacing(1),
        },
    },
    input: {
        display: 'none',
    },
}));

const UploadPages = () => {
    const classes = useStyles();

    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);

    // eslint-disable-next-line
    const [{ initialImg, croppedImg, result }, dispatch] = useUpload();

    const [open, setOpen] = useState(false);
    const [croppedSrc, setCroppedSrc] = useState();

    const { sendRequest } = useHttpClient();

    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    const saveCrop = () => {
        dispatch({
            type: 'cropImg',
            val: croppedSrc
        })
        handleClose();
    }

    const reportHandler = () => {
        var BASE64_MARKER = ';base64,';

        function DataToFile(dataURI) {
            var mime = dataURI.split(BASE64_MARKER)[0].split(':')[1];
            var filename = 'dataURI-file-' + (new Date()).getTime() + '.' + mime.split('/')[1];
            var bytes = atob(dataURI.split(BASE64_MARKER)[1]);
            var writer = new Uint8Array(new ArrayBuffer(bytes.length));

            for (var i = 0; i < bytes.length; i++) {
                writer[i] = bytes.charCodeAt(i);
            }

            return new File([writer.buffer], filename, { type: mime });
        }

        const getProba = async () => {
            let imgURL;
            if (croppedImg) {
                imgURL = croppedImg;
            } else {
                imgURL = initialImg;
            }
            // const user = CurrentUser();
            // const token = await user.getIdToken();
            const token = 'tanvi@gmail.com';
            const formData = new FormData();
            formData.append('image/jpeg', DataToFile(imgURL))
            formData.append('UID', token);
            formData.append('latitude', "34.351")
            formData.append('longitude', "42.43")
            setLoading(true);
            try {
                const result = await sendRequest(process.env.REACT_APP_BACKEND_URL + '/skinzy_app/get_17_proba', 'POST', formData);
                console.log(result)
                setLoading(false);
                dispatch({
                    type: 'setResult',
                    val: result
                })
            } catch (err) {
                console.log(err)
                setLoading(false);
                setError(true);
            }
        }
        getProba();
    }

    const onSelectFile = e => {
        if (e.target.files && e.target.files.length > 0) {
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                dispatch({
                    type: 'changeInitialImg',
                    img: reader.result
                })
            });
            reader.readAsDataURL(e.target.files[0]);
        }

    };

    let pages;
    if (result) {
        pages = (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center'
            }}>
                <Results />
                <Button
                    variant="contained"
                    color="primary"
                    style={{
                        textAlign: 'center',
                        width: '50%',
                        marginTop: '50px'
                    }}
                    onClick={() => dispatch({
                        type: 'resetAll'
                    })
                    }>
                    OK
                </Button>
            </div>
        )
    } else if (initialImg && !result) {
        pages = (
            <div>
                {loading && (
                    <LoadingSpinner size={100} thickness={4} />
                )}
                {!loading && (
                    <div style={{
                        paddingTop: '60px',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center'
                    }}>
                        <div style={{ marginTop: "20p", marginBottom: "40px" }}>
                            {error && (
                                <Alert onClose={() => { setError(false) }} severity="error">
                                    We encountered an issue. Please try again later.
                                </Alert>
                            )}
                        </div>
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                        }}>
                            {croppedImg ? (
                                <img src={croppedImg} alt="Selected" style={{ width: "80%", height: "10vh" }} />
                            ) : (
                                    <img src={initialImg} alt="Selected" style={{ width: "80%", maxHeight: "40vh" }} />
                                )}
                        </div>
                        <div style={{
                            paddingTop: '100px',
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                        }}>
                            <div style={{ marginTop: '30px' }}>
                                <Button onClick={handleClickOpen} variant="contained" color="primary" component="span">
                                    <CropIcon /> &nbsp; CROP
                                </Button>
                            </div>
                            <div style={{ marginTop: '40px' }}>
                                <Button onClick={reportHandler} variant="contained" color="primary" component="span">
                                    <ForwardIcon /> &nbsp; CONTINUE
                                </Button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        )
    } else {
        pages = (
            <div className="center" style={{ textAlign: "center", paddingTop: "100px" }}>
                <div className="center" style={{ textAlign: "center", padding: "30px", color: "#808080", fontSize: "20px" }}>
                    {/* <p>This web app is proprietary of Skinzy software solutions and intended for clinical image acquisition for research and development task.</p>
                    <p>Click on button below, crop affected part, choose disease or name a new disease and upload</p> */}
                </div>
                <div style={{ marginTop: "30px", marginBottom: "30px" }}>
                    <input
                        accept="image/*"
                        className={classes.input}
                        id="outlined-button-camera"
                        type="file"
                        onChange={onSelectFile}
                        capture
                    />
                    <label htmlFor="outlined-button-camera">
                        <Button variant="contained" color="primary" component="span">
                            <PhotoCamera /> &nbsp; CAMERA
                        </Button>
                    </label>
                </div>
                <Typography color='primary' style={{ fontSize: '17px', fontWeight: '600', textAlign: 'center' }}>
                    OR
                </Typography>
                <div style={{ marginTop: "50px", marginBottom: "30px" }}>
                    <input
                        accept="image/*"
                        className={classes.input}
                        id="outlined-button-file"
                        type="file"
                        onChange={onSelectFile}
                    />
                    <label htmlFor="outlined-button-file">
                        <Button variant="contained" color="primary" component="span">
                            <PhotoLibraryIcon /> &nbsp; CHOOSE IMAGE
                        </Button>
                    </label>
                </div>
            </div>
        )
    }

    return (
        <React.Fragment>
            <CropDialog open={open} saveCrop={saveCrop} handleClose={handleClose} croppedSrc={croppedSrc} setCroppedSrc={setCroppedSrc} />
            {pages}
        </React.Fragment>
    )
}

export default UploadPages;