import React from 'react';
import { useHistory } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';

const AppointmentItem = props => {
    let history = useHistory();

    const pushDoctor = () => {
        history.push(`/doctor/${props.id}`)
    }

    return (
        <React.Fragment>
            <Paper style={{ marginTop: '20px' }} onClick={pushDoctor}> 
                <div style={{ color: 'black', fontSize: '16px', fontWeight: '400', padding: '20px' }}>
                    Dr. {props.name}
                </div>
            </Paper>
        </React.Fragment>      
    )
}

const AppointmentList = props => {
    return (
        <div>
            {props.items.map(appointment => 
                    <AppointmentItem 
                        key={appointment.id}
                        id={appointment.id}
                        name={appointment.name}
                        dob={appointment.dob}
                        gender={appointment.gender}
                        email={appointment.email}
                        contact={appointment.contact}
                        is_doctor={appointment.is_doctor}
                        email_key={appointment.email_key}
                        doctor_upi_id={appointment.doctor_upi_id}
                    />
                )}
        </div>
    )
}

export default AppointmentList;