import React from 'react';
import { Formik } from 'formik';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import clsx from 'clsx';
import Name from '@material-ui/icons/AccountCircle';
import Home from '@material-ui/icons/Home';
import Phone from '@material-ui/icons/Phone';
import InputAdornment from '@material-ui/core/InputAdornment';

// import Gender from '@material-ui/icons/Wc';//gender icon
import Dob from '@material-ui/icons/CalendarToday';
const useStyles = makeStyles(theme => ({
    root: {
      height: '100vh',
    },
    root1:{
        '&:hover': {
          backgroundColor: 'transparent',
        },
      },
    icon: {
        borderRadius: '50%',
        width: 16,
        height: 16,
        boxShadow: 'inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)',
        backgroundColor: '#f5f8fa',
        backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))',
        '$root.Mui-focusVisible &': {
          outline: '2px auto rgba(19,124,189,.6)',
          outlineOffset: 2,
        },
        'input:hover ~ &': {
          backgroundColor: '#ebf1f5',
        },
        'input:disabled ~ &': {
          boxShadow: 'none',
          background: 'rgba(206,217,224,.5)',
        },
      },
      checkedIcon: {
        backgroundColor: '#137cbd',
        backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))',
        '&:before': {
          display: 'block',
          width: 16,
          height: 16,
          backgroundImage: 'radial-gradient(#fff,#fff 28%,transparent 32%)',
          content: '""',
        },
        'input:hover ~ &': {
          backgroundColor: '#106ba3',
        },
      },
      
    image: {
      backgroundImage: 'url(https://source.unsplash.com/random)',
      backgroundRepeat: 'no-repeat',
      backgroundColor:
        theme.palette.type === 'dark' ? theme.palette.grey[900] : theme.palette.grey[50],
      backgroundSize: 'cover',
      backgroundPosition: 'center',
    },
    paper: {
      margin: theme.spacing(8, 4),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%', 
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
    title: {
        flexGrow: 1,
    },
}));



function StyledRadio(props) {
    const classes = useStyles();
  
    return (
      <Radio
        className={classes.root1}
        disableRipple
        color="default"
        checkedIcon={<span className={clsx(classes.icon, classes.checkedIcon)} />}
        icon={<span className={classes.icon} />}
        {...props}
      />
    );
  }





const Register = () => {
    const classes = useStyles();
    return (
        <React.Fragment>
            <CssBaseline />
            <AppBar style={{ backgroundColor: '#02053b' }} position="static">
                <Toolbar>
                    <Typography variant="h6" className={classes.title}>
                       Register
                    </Typography>
                </Toolbar>
            </AppBar>
            <Container maxWidth='md'>
                <div style={{ marginTop: '10%' }}>
                    <Formik
                        initialValues={{
                            name: '',
                            dob: '2017-05-24',
                            gender: '',                                              
                            phone: '',
                            address: ''
                    }}
                        validate={values => {
                            const errors = {};
                            if (!values.address) {
                                errors.address = 'Required';
                            } else if (values.address.length < 12) {
                                errors.address = 'Address should be atleast 12 characters long'
                            }
                            if (!values.name) {
                                errors.name = 'Required';
                            } else if (values.name.length < 6) {
                                errors.name = 'Name should have atleast 6 characters'
                            }
                            
                            if (!values.phone) {
                                errors.phone = 'Required';
                            } else if (
                                values.phone.length !==9
                            ) {
                                errors.phone = 'Invalid phone number';
                            }

                            if (!values.gender) {
                                errors.gender = 'Required'
                            }
                            
                            if (!values.dob) {
                                errors.dob = 'Required'
                            }

                            return errors;
                        }}
                        onSubmit={(values, { setSubmitting }) => {
                            const submitFormHandler = async values => {
                            console.log(values);
                            }
                            submitFormHandler(values);
                        }}
                    >{({
                        values,
                        errors,
                        touched,
                        handleChange,
                        handleBlur,
                        handleSubmit,
                        isSubmitting,
                        setFieldValue
                    }) => (
                        <form className={classes.form} onSubmit={handleSubmit}>
                            <Grid container spacing={2}>
                            
                                <Grid item xs={10}>
                                <TextField
                                    
                                    fullWidth
                                   
                                    label="Your Name"
                                    name="name"
                                    type="text"
                                    InputProps={{
                                      startAdornment: (
                                        <InputAdornment position="start">
                                          <Name />
                                        </InputAdornment>
                                      ),
                                    }}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.name}
                                    
                                />
                                <div style={{ margin: "10px", color: "red" }}>
                                    {errors.name && touched.name && errors.name}
                                </div>
                                </Grid>
                                <Grid item xs={12}>
                                  
                                <TextField  
                                      name="dob"
                                      label="Birthday"
                                      type="date"
                                      onChange={handleChange}
                                      onBlur={handleBlur}
                                      value={values.dob}
                                      className={classes.textField}
                                      InputProps={{
                                        startAdornment: (
                                          <InputAdornment position="start">
                                            <Dob />
                                          </InputAdornment>
                                        ),
                                      }}

                                />
                                <div style={{ margin: "10px", color: "red" }}>
                                    {errors.dob && touched.dob && errors.dob}
                                </div>
                                </Grid>
                               
                               
                                <Grid item xs={10}>
                  
                                <TextField
                                    fullWidth
                                    label="Address"
                                    name="address"
                                    type="text"
                                    InputProps={{
                                      startAdornment: (
                                        <InputAdornment position="start">
                                          <Home />
                                        </InputAdornment>
                                      ),
                                    }}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.address}
                                />
                                <div style={{ margin: "10px", color: "red" }}>
                                    {errors.address && touched.address && errors.address}
                                </div>
                                </Grid>
                                

                               
                                <Grid item xs={10}>
                                <TextField
                                 
                                     label="PhoneNumber"
                                     name="phone"
                                     type="text"
                                     onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.phone}
                                    InputProps={{
                                      startAdornment: (
                                        <InputAdornment position="start">
                                          <Phone />
                                        </InputAdornment>
                                      ),
                                    }}
                                    />
                                <div style={{ margin: "10px", color: "red" }}>
                                    {errors.phone && touched.phone && errors.phone}
                                </div>
                                </Grid>
                                <Grid item xs={12}>
                                
                                <FormControl component="fieldset">
                                 <FormLabel component="legend" >Gender</FormLabel>
                                <RadioGroup aria-label="gender" name="gender" 
                                
                                
                                onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.gender}
                                   >
                                <FormControlLabel value="female" control={<StyledRadio />} label="Female"  />
                                <FormControlLabel value="male" control={<StyledRadio />} label="Male" />
                                <div style={{ margin: "10px", color: "red" }}>
                                    {errors.gender && touched.gender && errors.gender}
                                </div>
                                </RadioGroup>
                               
                                </FormControl>
                                </Grid>
                            </Grid>
                            <Button
                                type="submit"
                            
                                fullWidth
                                variant="contained"
                                style={{ backgroundColor: '#02053b', color: 'white' }}
                                className={classes.submit}
                                disabled={isSubmitting} 
                            >
                                Submit
                            </Button>
                            <Grid container>
                        </Grid>
                        </form>
                    )}
                    </Formik>
                    
                </div>
            </Container>
        </React.Fragment>
    )
}

export default Register;
