import React, { useState, useEffect } from 'react';
import { useHttpClient } from '../hooks/http-hook';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import AppointmentList from './AppointmentList';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import LoadingSpinner from '../utils/LoadingSpinner';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
}));

const Appointments = () => {
    const classes = useStyles();
    const [loadedAppointments, setLoadedAppointments] = useState();

    // eslint-disable-next-line
    const { sendRequest } = useHttpClient();

    useEffect(() => {
        const fetchAppointments = async () => {
            try {
                const responseData = await sendRequest(
                    process.env.REACT_APP_BACKEND_URL + '/skinzy_app/get_doctor',
                );
                console.log(responseData)
                setLoadedAppointments(responseData);
            } catch (err) { }
        };
        fetchAppointments();
        // setLoadedAppointments([
        //     {   "name": "Amurto",
        //         "dob": 10102000,
        //         "gender": "male",
        //         "id": 1,
        //         "email": "test1@gmail.com",
        //         "contact": "123456789",
        //         "is_doctor": true,
        //         "email_key": "123456789",
        //         "doctor_upi_id": "abc@oksbi"
        //     },
        //     {   "name": "Pratik",
        //         "dob": 10102000,
        //         "gender": "male",
        //         "id": 2,
        //         "email": "test1@gmail.com",
        //         "contact": "123456789",
        //         "is_doctor": true,
        //         "email_key": "123456789",
        //         "doctor_upi_id": "abc@oksbi"
        //     },
        //     {   "name": "Shubham",
        //         "dob": 10102000,
        //         "gender": "male",
        //         "id": 3,
        //         "email": "test1@gmail.com",
        //         "contact": "123456789",
        //         "is_doctor": true,
        //         "email_key": "123456789",
        //         "doctor_upi_id": "abc@oksbi"
        //     },
        //     {   "name": "Gavin",
        //         "dob": 10102000,
        //         "gender": "male",
        //         "id": 4,
        //         "email": "test1@gmail.com",
        //         "contact": "123456789",
        //         "is_doctor": true,
        //         "email_key": "123456789",
        //         "doctor_upi_id": "abc@oksbi"
        //     },
        // ]);
    }, [sendRequest]);

    return (
        <React.Fragment>
            <CssBaseline />
            <AppBar style={{ backgroundColor: '#02053b' }} position="static">
                <Toolbar>
                    <Typography variant="h6" className={classes.title}>
                        Book Your Appointment
                    </Typography>
                </Toolbar>
            </AppBar>
            <Container maxWidth='md'>
                {!!loadedAppointments && <AppointmentList items={loadedAppointments} />}
                {!loadedAppointments && <LoadingSpinner size={100} thickness={4} />}
            </Container>
        </React.Fragment>
    )
}

export default Appointments;
