import React from 'react'

import firebase from "firebase/app";
import "firebase/auth";
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';

function CurrentURL() {
    const url = window.location.pathname;
    console.log(url);
    let extracted = url;
    if (extracted === '/')
        return '/';
    if (extracted[0] === '/')
        extracted = extracted.substring(1);
    extracted = extracted.split('/');
    return `/${extracted[0]}/`;
}
const init_url = CurrentURL();
console.log(init_url);
// Configure FirebaseUI.
const uiConfig = {
    signInFlow: 'redirect',
    // Redirect to /signedIn after sign in is successful. Alternatively you can provide a callbacks.signInSuccess function.
    signInSuccessUrl: init_url,
    // We will display Google and Facebook as auth providers.
    signInOptions: [
        {
            provider: firebase.auth.GoogleAuthProvider.PROVIDER_ID,
            scopes: [
                'https://www.googleapis.com/auth/user.birthday.read',
                'https://www.googleapis.com/auth/user.addresses.read',
                'https://www.googleapis.com/auth/user.phonenumbers.read'
            ],
            customParameters: {
                // Forces account selection even when one account
                // is available.
                prompt: 'select_account'
            }
        }
    ],
    tosUrl: 'https://help.github.com/en/github/site-policy/github-terms-of-service',
    // Privacy policy url/callback.
    privacyPolicyUrl: 'http://www.skinzy.in/policy_page'
};

export default function Login() {
    return (
        <div className="center" style={{ textAlign: "center", paddingTop: "60px" }}>
            <img src={`${init_url}logo192.png`} alt="Logo" />
            <br />
            <h2><b>Scan. Upload. Get Results.</b></h2>
            <div className="center" style={{ textAlign: "center", padding: "30px", color: "#808080", fontSize: "20px" }}>
                <p>This web app uses AI based technologies to detect Skin Diseases</p>
            </div>
            <div style={{ display: "inline-block", height: "50%", width: "100%" }}>
                <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={firebase.auth()} />
            </div>
        </div>
    );
}   