import React from 'react';
import { Redirect } from "react-router-dom";

import CurrentUser from "./Current";

export function ShowIfAuth(props) {
    if (CurrentUser() != null)
        return <>{props.children}</>;
    else
        return <></>;
}

export function ShowIfNoAuth(props) {
    if (CurrentUser() == null)
        return <>{props.children}</>;
    else
        return <></>;
}

export function AuthenticatedOnly(props) {
    return <>
        <ShowIfAuth>
            {props.children}
        </ShowIfAuth>
        <ShowIfNoAuth>
            <Redirect to="/" />
        </ShowIfNoAuth>
    </>;
};