import React, { useState, useEffect } from "react";
import { useHttpClient } from '../hooks/http-hook';
import LoadingSpinner from '../utils/LoadingSpinner';
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import CssBaseline from "@material-ui/core/CssBaseline";
import Container from "@material-ui/core/Container";
import Paper from "@material-ui/core/Paper";

const PrescriptionItem = props => {
    return (
        <Paper elevation={3} style={{ marginBottom: '25px', paddingTop: '5px', paddingBottom: '5px' }}>
            <div style={{ textAlign: 'left', padding: '10px' }}>
                <Typography style={{ fontWeight: '600', fontSize: '16px' }}>
                    Medicine
                </Typography>
                <Typography style={{ fontSize: '16px' }}>
                    {props.medicine}
                </Typography>
                <hr></hr>
                <Typography style={{ fontWeight: '600', fontSize: '16px' }}>
                    Prescription
                </Typography>
                <Typography variant="body2" component="p">
                    {props.prescription}
                </Typography>
            </div>
        </Paper>
    )
}

const PrescriptionList = props => {
    return (
        <div style={{ marginBottom: '5px', marginTop: '5px', paddingTop: '5px', paddingBottom: '5px', textAlign: 'center' }}>
        {props.items.map(pres =>
            <PrescriptionItem
                id={pres.id}
                medicine={pres.medicine}
                prescription={pres.prescription}
            />
        )}
        </div>
    )
}

const Prescription = props => {
    const { isLoading, sendRequest } = useHttpClient();
    const [loadedPrescription, setLoadedPrescription] = useState();

    useEffect(() => {
        // const fetchPrescription = async () => {
        //     try {
        //         let formData = new FormData();
        //         formData.append("pat_id", "1");
        //         const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL + '/skinzy_app/get_prescription', 'POST', formData);
        //         console.log(responseData);
        //         setLoadedPrescription(responseData);
        //     } catch (err) { }
        // };
        // fetchPrescription();
        setLoadedPrescription([
            {
                id:'1',
                medicine  :'Bactrin',
                prescription:' for 2 days in morning',
            },
            {
                id:'2',
                medicine  :'neosporin',
                prescription:'for 2 days in  morning and evening',
            },
            {
                id:'3',
                medicine  :'septra',
                prescription:' for 2 days in morning',
            },
            {
                id:'4',
                medicine  :'Fungicros',
                prescription:' Two times in a day ',
            },
        ])
    }, [sendRequest]);

    return (
        <React.Fragment>
            <CssBaseline />
            <AppBar style={{ backgroundColor: "#02053b" }} position="static">
                <Toolbar>
                    <Typography variant="h6">
                        Suggested Prescriptions
                    </Typography>
                </Toolbar>
            </AppBar>
            <div style={{ backgroundColor: '#d3d3d3', minHeight: '90vh', maxHeight: '90vh', overflow: 'scroll' }}>
                <Container maxWidth="md" >
                    {isLoading && <LoadingSpinner size={100} thickness={4} />}
                    {!isLoading && loadedPrescription && <PrescriptionList items={loadedPrescription} />}
                </Container>
            </div>
        </React.Fragment>
    );
};

export default Prescription;
