import React from 'react';
import { useHistory } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';

const AppointmentItem = props => {
    let history = useHistory();

    const pushAppointment = () => {
        history.push(`/Appointmentsmade/${props.id}`)
    }

    return (
        <React.Fragment>
            <Paper style={{ marginTop: '20px' }} onClick={pushAppointment}> 
                <div style={{ color: 'black', fontSize: '16px', fontWeight: '400', padding: '20px' }}>
                    Dr. {props.name} on : {props.timee}  from :{props.from} to {props.to}
                    <Grid >
                        <Button variant="contained" color="primary" >
                            Cancel
                        </Button>
                        </Grid>
                </div>
            </Paper>
        </React.Fragment>      
    )
}

const AppointmentList = props => {
    return (
        <div>
            {props.items.map(appointment => 
                    <AppointmentItem 
                        key={appointment.id}
                        id={appointment.id}
                        name={appointment.name}
                        dob={appointment.dob}
                        gender={appointment.gender}
                        email={appointment.email}
                        timee={appointment.timee}
                        from={appointment.from}
                        to={appointment.to}
                        is_doctor={appointment.is_doctor}
                        email_key={appointment.email_key}
                        doctor_upi_id={appointment.doctor_upi_id}
                    />
                )}
        </div>
    )
}

export default AppointmentList;