import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Rating from '@material-ui/lab/Rating';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';

const labels = {
    0.5: 'Useless',
    1: 'Useless+',
    1.5: 'Poor',
    2: 'Poor+',
    2.5: 'Ok',
    3: 'Ok+',
    3.5: 'Good',
    4: 'Good+',
    4.5: 'Excellent',
    5: 'Excellent+',
};

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        height: '100vh',
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: 200,
        },
    },
    appBar: {
        position: 'relative',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


const FeedbackDialog = props => {
    const [value, setValue] = useState(2);
    const [hover, setHover] = useState(-1);
    const classes = useStyles();

    const [text, setText] = useState('');

    const handleChange = event => {
        setText(event.target.value);
    };

    return (
        <Dialog fullScreen open={props.feedback} onClose={props.handleCloseFeedback} TransitionComponent={Transition}>
        <AppBar className={classes.appBar}>
            <Toolbar>
                <IconButton edge="start" color="inherit" onClick={props.handleCloseFeedback} aria-label="close">
                <CloseIcon />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                    Feedback
                </Typography>
            </Toolbar>
            </AppBar>
            <div className={classes.root}>
                <Typography variant='h6' style={{ padding: '15px' }}>
                    Please consider leaving a feedback of your experience
                </Typography>
                <Typography style={{ color: '#3f51b5', fontSize: '50px', fontWeight: '600', marginTop: '20px' }}>
                    {value}
                </Typography>
                <h3 style={{ color: '#3f51b5', fontSize: '20px', fontWeight: '600', marginTop: '20px', marginBottom: '30px' }}>
                    {value !== null && <Box ml={2}>{labels[hover !== -1 ? hover : value]}</Box>}
                </h3>
                <Rating
                    name="hover-feedback"
                    value={value}
                    precision={0.5}
                    style={{
                        fontSize: '40px'
                    }}
                    onChange={(event, newValue) => {
                        setValue(newValue);
                    }}
                    onChangeActive={(event, newHover) => {
                        setHover(newHover);
                    }}
                />
                
                <TextField
                    id="standard-textarea"
                    label="Type your feedback here.."
                    style={{ marginTop: '80px', width: '70%' }}
                    value={text}
                    onChange={handleChange}
                    multiline
                />
                <Grid style={{ marginTop: '80px' }} container spacing={0}>
                    <Grid item xs={6} style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center'
                    }}>
                        <Button style={{ width: '70%' }} variant="contained" color="primary" onClick={props.handleCloseFeedback}>
                            CANCEL
                        </Button>
                    </Grid>
                    <Grid item xs={6} style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center'
                    }}>
                        <Button style={{ width: '70%' }} variant="contained" color="primary">
                            SUBMIT
                        </Button>
                    </Grid>
                </Grid>
            </div>
        </Dialog>
    );
}

export default FeedbackDialog;