import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

import firebase from "firebase/app";
import 'firebase/auth';

import { FirebaseConfig } from "./firebase.config";

async function FirebaseSetup() {
    // eslint-disable-next-line
    async function CreateForm(val) {
        var formdata = new FormData();
        formdata.append("doc_id", "1");
        formdata.append("date", `2020-${Math.trunc(4 + val / 30)}-${val % 30}`);
        console.log(formdata.get('date'));
        formdata.append("start_time", "[12:45]");
        formdata.append("end_time", "[1:50]");
        formdata.append("fees", "[0]");

        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        const r = await (await fetch(process.env.REACT_APP_BACKEND_URL + "/skinzy_app/set_appointment_for_doctor", requestOptions)).text()
        console.log(r);
    }
    // for (let index = 10; index < 30; index++) {
    //     await CreateForm(index);
    // }

    // Wait till Firebase Load Complete before Firing
    function getCurrentUser(auth) {
        return new Promise((resolve, reject) => {
            const unsubscribe = auth.onAuthStateChanged(user => {
                unsubscribe();
                resolve(user);
            }, reject);
        });
    }
    // If you want your app to work offline and load faster, you can change
    // unregister() to register() below. Note this comes with some pitfalls.
    // Learn more about service workers: https://bit.ly/CRA-PWA
    serviceWorker.register();

    firebase.initializeApp(FirebaseConfig);
    await getCurrentUser(firebase.auth());
}

FirebaseSetup().then(async () => {
    ReactDOM.render(<App />, document.getElementById('root'));
})